import React, { useState } from 'react'
import { useEffect } from 'react';
import { Link } from 'react-router-dom';

const Footer = () => {
    var [Message, setMessage] = useState(false)
    var [Color,setColor] = useState(false)
    const [Newsletter, setNewsletter] = useState({
        name: "",
        email: ""
    });
    const changeHandler = (e) => {
        var name = e.target.name
        var value = e.target.value
        setNewsletter((old) => {
            return { ...old, [name]: value }
        })
    }


    async function postData(e) {
        e.preventDefault()
         var response = await fetch("/newsletter",{
            method:"post",
            headers:{"content-type":"application/json"},
            body: JSON.stringify({name:Newsletter.name, email:Newsletter.email})
         })
       response = await response.json()
       if (response.result === "Done") {
           setColor("alert-success")
        setMessage(response.message)
        setNewsletter({})
       }else{
           setColor("alert-danger")
        setMessage(response.message)
       }
    }

    useEffect(() => {
    var timeout = setTimeout(() => {
        setMessage(false)
    }, 3000);

        return () => { clearTimeout(timeout) }
    }, [Message]);

     

    return (
        <>
            {/* <!-- Footer Start --> */}
            <div className="container-fluid bg-secondary text-dark mt-5 pt-5">
                <div className="row px-xl-5 pt-5">
                    <div className="col-lg-4 col-md-12 mb-5 pr-3 pr-xl-5">
                        <a href="" className="text-decoration-none">
                            <h1 className="mb-4 display-5 font-weight-semi-bold"><span className="text-primary font-weight-bold border border-white px-2 mr-1">Quick</span>Shop</h1>
                        </a>
                        <p>Dolore erat dolor sit lorem vero amet. Sed sit lorem magna, ipsum no sit erat lorem et magna ipsum dolore amet erat.</p>
                        <p className="mb-2"><i className="fa fa-map-marker-alt text-primary mr-3"></i>Noida Sector-16, Near McDonald's</p>
                        <p className="mb-2"><i className="fa fa-envelope text-primary mr-3"></i>quick@example.com</p>
                        <p className="mb-0"><i className="fa fa-phone-alt text-primary mr-3"></i>+012 345 67890</p>
                    </div>
                    <div className="col-lg-8 col-md-12">
                        <div className="row">
                            <div className="col-md-4 mb-5">
                                <h5 className="font-weight-bold text-dark mb-4">Quick Links</h5>
                                <div className="d-flex flex-column justify-content-start">
                                <Link className="text-dark mb-2" to="/"><i className="fa fa-angle-right mr-2"></i>Home</Link>
                                    <Link className="text-dark mb-2" to="/shop"><i className="fa fa-angle-right mr-2"></i>Our Shop</Link>
                                    <Link className="text-dark mb-2" to="/contact"><i className="fa fa-angle-right mr-2"></i>Contact Us</Link>
                                    <Link className="text-dark" to="/about"><i className="fa fa-angle-right mr-2"></i>About Us</Link>
                                </div>
                            </div>
                            <div className="col-md-4 mb-5">
                                <h5 className="font-weight-bold text-dark mb-4">Quick Links</h5>
                                <div className="d-flex flex-column justify-content-start">
                                    <Link className="text-dark mb-2" to="/"><i className="fa fa-angle-right mr-2"></i>Home</Link>
                                    <Link className="text-dark mb-2" to="/shop"><i className="fa fa-angle-right mr-2"></i>Our Shop</Link>
                                    <Link className="text-dark mb-2" to="/contact"><i className="fa fa-angle-right mr-2"></i>Contact Us</Link>
                                    <Link className="text-dark" to="/about"><i className="fa fa-angle-right mr-2"></i>About Us</Link>
                                </div>
                            </div>
                            <div className="col-md-4 mb-5">

                                <h5 className="font-weight-bold text-dark mb-4">Newsletter</h5>
                                {Message ?
                                    <p className={`alert ${Color}`}>
                                        {Message}
                                    </p>
                                    : ""
                                }
                                <form action="" onSubmit={postData}>
                                    <div className="form-group">
                                        <input type="text" value={Newsletter.name !== undefined ? Newsletter.name : ""} className="form-control border-0 py-4" placeholder="Your Name"
                                            name='name' required="required" onChange={changeHandler} />
                                    </div>
                                    <div className="form-group">
                                        <input type="email" className="form-control border-0 py-4" placeholder="Your Email"
                                            value={Newsletter.email !== undefined ? Newsletter.email : ""} name='email' required="required" onChange={changeHandler} />
                                    </div>
                                    <div>
                                        <button className="btn btn-primary text-light btn-block border-0 py-3" type="submit">Subscribe Now</button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row border-top border-light mx-xl-5 py-4">
                    <div className="col-md-6 px-xl-0">
                        <p className="mb-md-0 text-center text-md-left text-dark">
                            &copy; <Link className="text-dark font-weight-semi-bold" to="/">QuickShop</Link>. All Rights Reserved. Designed
                            by
                            <a className="text-dark font-weight-semi-bold" href="https://github.com/dineshbhati61102"> DineshBhati </a><br />
                            Distributed By <a href="https://themewagon.com" target="_blank">Theme  Wagon</a>
                        </p>
                    </div>
                    <div className="col-md-6 px-xl-0 text-center text-md-right">
                        <img className="img-fluid" src="assets/img/payments.png" alt="" />
                    </div>
                </div>

            </div>
            {/* <!-- Footer End --> */}


            {/* <!-- Back to Top --> */}
            <a href="#" className="btn btn-primary text-light back-to-top"><i className="fa fa-angle-double-up"></i></a>



        </>
    )
}

export default Footer


