import React, { useEffect, useState } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import LeftNav from './LeftNav'
import { useSelector, useDispatch } from 'react-redux'
import { GetContactAction, DeleteContactAction, UpdateContactAction} from '../../Store/Actions/ContactAction'

const AdminSingleContact = () => {
   var navigate = useNavigate()
   var {_id} = useParams()
   var allContacts = useSelector((state)=> state.ContactStateData)
  const [Contact, setContact] = useState([]);
   var dispatch = useDispatch()


    function getApiData() {
      dispatch(GetContactAction())
      var item = allContacts.find((item)=>item._id === _id)
      if(item){
         setContact(item)
      }}

      function updateRecord() {
        dispatch(UpdateContactAction({...Contact, status:"Done"}))
         setContact((old)=>{
            return{...old,['status']:"Done"}
         })
      }

      function deleteRecord() {
         dispatch(DeleteContactAction({_id}))
         navigate("/admin-contacts")
      }

useEffect(() => {
   getApiData()
}, [allContacts.length]);
  return (
    <>
      <div className='container-fluid p-0'>
    <div className="row m-0 p-0">
    <div className="col-sm-2 p-0 mt-5 left_navbar p-0 m-0">
          <LeftNav />
    </div>

    <div className="col-sm-10 m-0 mt-5">
    <div className="container-fluid single_contact">
      <h5 className='bg-primary text-center text-light py-1'>Single Contact</h5>
      <div className='d-flex'>
               <div className='border  p-3 w-50'>
                  ID
               </div>
               <div className='border  p-3 w-50'>
                 {Contact._id}
               </div>
            </div>

            <div className='d-flex'>
               <div className='border  p-3 w-50'>
                  Name
               </div>
               <div className='border  p-3 w-50'>
               {Contact.name}
               </div>
            </div>

            <div className='d-flex'>
               <div className='border  p-3 w-50'>
                  Email
               </div>
               <div className='border  p-3 w-50'>
               {Contact.email}
               </div>
            </div>

            <div className='d-flex'>
               <div className='border  p-3 w-50'>
                 Phone
               </div>
               <div className='border  p-3 w-50'>
               {Contact.phone}
               </div>
            </div>

            <div className='d-flex'>
               <div className='border  p-3 w-50'>
                  Subject
               </div>
               <div className='border  p-3 w-50'>
               {Contact.subject}
               </div>
            </div>

            <div className='d-flex'>
               <div className='border  p-3 w-50'>
                  Message
               </div>
               <div className='border  p-3 w-50'>
               {Contact.message}
               </div>
            </div>

            <div className='d-flex'>
               <div className='border  p-3 w-50'>
                  Date
               </div>
               <div className='border  p-3 w-50'>
                  {Contact.date}
               </div>
            </div>

            <div className='d-flex'>
               <div className='border  p-3 w-50'>
                  Status
               </div>
               <div className='border  p-3 w-50'>
                  {Contact.status}
               </div>
               </div>

               <div className="row mt-5">
               <div className='col-12 update_btn'>
               { Contact.status === "Active" ?
                  <button className='btn btn-primary w-100  text-light' onClick={updateRecord}>Update Status to Done</button>:
                  <button className='btn btn-primary w-100  text-light' onClick={deleteRecord}>Delete</button>
                }
                </div>
            </div>
            </div>
        </div>
       </div>
       </div>
    </>
  )
}

export default AdminSingleContact
