import React,{useState, useEffect} from 'react'
import { Link, useNavigate } from 'react-router-dom'
import LeftNav from './LeftNav'


const AdminHome = () => {
  var [User, setUser] = useState({});
  var navigate = useNavigate()

    async function getApiData() {
      var response = await fetch("/user/admin/" + localStorage.getItem("UserId"),{
        method:"get",
        headers:{"content-type":"application/json",
          "authorization": localStorage.getItem("token")}
      })
      response = await response.json()

      if (response.result === "Done") {
          setUser(response.data)
      }else{
        navigate("/login")
      }
    }

useEffect(() => {
  getApiData()
}, []);
  return (
    <>
    
    <div className='container-fluid p-0'>
    <div className="row m-0 p-0">
    <div className="col-sm-2 mt-5 p-0 left_navbar p-0 m-0">
          <LeftNav />
    </div>

<div className="col-sm-10 m-0">
<div className="container">
<div className='row'>
<div className='col d-flex p-3 admin_pic'>
  <img src={`/public/users/${User.pic}`} className='img-fluid'  alt={`${User.name} Image`} 
     style={{border:"5px solid grey", width:"150px", height:"150px"}}
  />
<h2 className='font-weight-bold mx-5 mt-5 text-primary text-capitalize'>welcome, Admin</h2>
</div> 
</div> 
</div>

    <div className="admin_info">
           <div className='d-flex'>
                <div className='border  p-3 w-50'>Name</div>
               <div className='border  p-3 w-50'>{User.name}</div>
              </div>
              <div className='d-flex'>
                <div className='border  p-3 w-50'>User Name</div>
               <div className='border  p-3 w-50'>{User.username}</div>
              </div>
              <div className='d-flex'>
                <div className='border  p-3 w-50'>Email</div>
               <div className='border  p-3 w-50'>{User.email}</div>
              </div>
              <div className='d-flex'>
                <div className='border  p-3 w-50'>Phone</div>
               <div className='border  p-3 w-50'>{User.phone}</div>
              </div>
              <div className='d-flex'>
                <div className='border  p-3 w-50'>Role</div>
               <div className='border  p-3 w-50'>{User.role}</div>
              </div>
              <div className='d-flex mt-4'>
                <Link to={`/update-profile/${User._id}`} className='btn btn-primary text-light '>Update Profile</Link>
              </div>
              </div>
            </div>
            </div>
            </div> 

    </>
  )
}

export default AdminHome



